<template>
	<view>
		<view>
			<u-popup :show="show" @close="close" round="40">
				<view style="margin: 20rpx;">
					<view style="margin: 20rpx auto;">
						<u--text :bold="true" size="34" align="center" text="选择开始时间"></u--text>
					</view>
					<view>
						<u-tabs :list="dayList" @click="tabsClick" lineWidth="140rpx" lineHeight="4rpx"
							:activeStyle="activeStyle" :itemStyle="itemStyle" :scrollable="false"></u-tabs>

						<scroll-view scroll-y="true" style="height: 600rpx;">
							<u-cell-group>
								<!-- <view v-if="current"> -->
									<u-cell v-for="(list,indexList) in timeList" :key="indexList">
										<view slot="title" class="time">
											<view class="time-item" v-for="(array,indexArray) in list" :key="indexArray"  :style="indexArray!==0?'margin-left:20rpx':''">
												<u-tag size="large"  :text="array.time"  borderColor="#ffffff" :plain="array.checked" color="#000000"  type="primary" :name="indexArray"
													@click="radioClick(array.time)">
												</u-tag>
											</view>
										</view>
									</u-cell>	
								<!-- </view> -->
								<!-- <view v-else>
									<u-cell v-for="(list,indexList) in timeList" :key="indexList">
										<view slot="title" class="time">
											<view class="time-item" v-for="(array,indexArray) in list" :key="indexArray"  :style="indexArray!==0?'margin-left:70rpx':''">
												<u-tag size="large" :text="array.time"  borderColor="#ffffff" :plain="array.checked" color="#000000"  type="primary" :name="indexArray"
													@click="radioClick(array.time)">
												</u-tag>
											</view>
										</view>
									</u-cell>
								</view> -->
							</u-cell-group>
						</scroll-view>
						

					</view>
				</view>
			</u-popup>
		</view>
	</view>
</template>

<script>
	export default {
		name: "my-popup",
		props: {
			show: {
				default: false,
				type: Boolean
			}

		},
		data() {
			return {
				dayList: [],
				itemStyle: {
					textAlign: 'center',
					width: '140rpx',
					paddingBottom: ' 20rpx',
					lineHeight: '50rpx'
				},
				activeStyle: {
					color: '#35a5ed',
					fontWeight: 'bold'
				},
				timeList:[],
				current:false,
				allTimeList:[]
			};
		},
		methods: {
			close() {
				this.$emit('closeTime')
			},
			radioClick(time) {
				const _this = this
				console.log(this.current)
				console.log(time)
				// if(this.current == 0){
					_this.timeList.forEach(function(list,index){
						list.forEach(function(item,key){
							item.checked = time === item.time ? false: true
						})
					})	
				// }else{
				// 	_this.allTimeList.forEach(function(list,index){
				// 		list.forEach(function(item,key){
				// 			item.checked = time === item.time ? false : true
				// 		})
				// 	})
				// }
				
			},
			getTimeList(){
				var time = new Date();
				var now = new Date();
				if (time.getHours() <8) {
					time.setHours(8)
					time.setMinutes(0)
				}
				if(time.getMinutes()<=30&&time.getMinutes()>0) {
					time.setMinutes(30)
				}
				else{
					time.setMinutes(0)
					time.setHours(time.getHours()+1)
				}
				while(time.getHours() !==23){
					var a=[];
					for (var i=0; i< 4; i++) {
						if(time.getHours() ===23) break;
						a.push({
							time: uni.$u.date(time.valueOf(),'hh:MM'),
							checked: true,
							show: time<now?false:true,
						})
						time.setMinutes(time.getMinutes()+30)
					}
					this.timeList.push(a)
				}
				console.log(this.timeList)
				
			},
			tabsClick(item){
				console.log(item.timestamp)
				if(item.name === this.dayList[0].name){
					// this.timeList = this.allTimeList
					this.current = false;
				}
				else{
					this.current = true;
					// this.getTimeList()
				}
				console.log(this.current)
			},
			setDayList(){
				var weekArr =['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六']
				var day = new Date()
				var now = new Date()
				if(parseInt(day.getHours()) < 23)
				{
					this.dayList.push({
						name: uni.$u.date(day.valueOf(),'mm月dd日')+'\n'+ weekArr[day.getDay()],
						timestamp: day.valueOf()
					})
				}
				day.setHours(day.getHours()+24)
				this.dayList.push({
					name: uni.$u.date(day.valueOf(),'mm月dd日')+"\n"+ weekArr[day.getDay()],
					timestamp: day.valueOf()
				})
				day.setHours(day.getHours()+24)
				this.dayList.push({
					name: uni.$u.date(day.valueOf(),'mm月dd日')+"\n"+ weekArr[day.getDay()],
					timestamp: day.valueOf()
				})
			}
			
		},
		onReady() {
			this.setDayList()
			this.getTimeList()
		}
	}
</script>

<style lang="scss">
	.time {
	
		.time-item {
			display: inline-block;
			margin: 20rpx auto;
			font-size: 34rpx;
		}
	}
</style>
